    //for datePicker

    //for the select option with filter starts

    transform( items: any[], searchTerm: string, labelKey?: string ): any {
        if ( !items || !searchTerm ) {
            return items;
        }

        return items.filter(
            item =>
                item[labelKey || 'label']
                    .toLowerCase()
                    .includes( searchTerm.toLowerCase() ) === true
        );
    }

    //for the select option with filter ends

    //for popup modal starts 
    openSpecialRights( contentSpecialRights ) {
        this.modalService.open( contentSpecialRights, { centered: true, } ).result.then(
            resultSpecialRights => {
                this.closeResultSpecialRights = `Closed with: ${resultSpecialRights}`;
            },
            reason => {
                this.closeResultSpecialRights = `Dismissed ${this.getDismissReason( reason )}`;
            }
        );
    }


    private getDismissReason( reason: any ): string {
        if ( reason === ModalDismissReasons.ESC ) {
            return 'by pressing ESC';
        } else if ( reason === ModalDismissReasons.BACKDROP_CLICK ) {
            return 'by clicking on a backdrop';
        } else {
            return `with: ${reason}`;
        }
    }

