    setLoginTimeMode( searchBy: string ) {
        if ( searchBy === 'true' ) {
            this.setLoginTimeFromTime = true;
            this.setLoginTimeToTime = true;


        } else if ( searchBy === 'false' ) {
            this.setLoginTimeFromTime = false;
            this.setLoginTimeToTime = false;


        } else {
            this.setLoginTimeFromTime = false;
            this.setLoginTimeToTime = false;
        }
    }

